import { Component, Input } from '@angular/core';
import { BaseTooltip } from './base-tooltip';

const CROSSHAIR_TOOLTIP_CLASS = 'k-chart-crosshair-tooltip';
/**
 * @hidden
 */
@Component({
  selector: 'kendo-chart-crosshair-tooltip',
  template: `
    <kendo-popup [offset]="offset" [popupAlign]="align" [animate]="false" *ngIf="active" [collision]="collision" >
        <div [ngClass]="popupClasses" [ngStyle]="style">
            {{ value }}
        </div>
    </kendo-popup>
    `
})
export class CrosshairTooltipComponent extends BaseTooltip {

  @Input()
  public key: string;

  protected value: any;

  public show(e: any): void {
    super.show(e);

    this.popupClasses[CROSSHAIR_TOOLTIP_CLASS] = true;

    this.value = e.value;
  }
}



